import React, {Component} from 'react';
import './timerout.css';
class TimeOut extends Component {
    constructor(props) {
        super(props);
        // this.state={hours:0, minutes:0, second:0}
    }

    componentWillMount() {
        this.getTime();
        this.timeer = setInterval(this.getTime, 1000)
    }
    getTime = ()=>{
        let nowData = new Date();
        let privDate = new Date(new Date().setHours(...this.props.datumTime));
        let _second = Math.floor((privDate - nowData)/1000);
        this.props.getTimeValue(_second);
        if (_second < 0) {
            _second +=86400;
        }
        let hours = Math.floor(_second / 3600)<10 ? '0'+Math.floor(_second / 3600) :  Math.floor(_second / 3600);
        let minutes =  Math.floor((_second%3600) / 60)<10 ? '0'+Math.floor((_second%3600) / 60) : Math.floor((_second%3600) / 60);
        let second =  Math.floor(_second%3600%60)<10? '0'+Math.floor(_second%3600%60) : Math.floor(_second%3600%60);
        this.setState({hours, minutes, second});
        // console.log(this.state)
    }
    componentWillUnmount(){
        clearInterval(this.timeer);
    }
    render() {
        return (
            this.props.hasTimer ?  (<span className="timerSpan">
                <span>{this.state.hours}</span>:<span>{this.state.minutes}</span>:<span>{this.state.second}</span>
            </span>):  (<span className="timerSpanRed">
                <span>00</span>:<span>00</span>:<span>00</span>
            </span>)
        )
    }
}
export default TimeOut


