import React, {Component} from 'react';
import './goodsDetail.css'
import defaultModule from '../../components/Module/defaultModule/defaultModule';
import showModule from '../../components/Module/showModule';
import {weChatPay} from '../../components/WechatPay/wechatPay';
import TimeOut from '../../components/TimeOut/TimeOut';
import FiveTwoZeroModule from '../../components/Module/520/FiveTwoZeroModule'
import {getSkuInfo,submitActivityOrder} from '../../services/api'
export default class GoodsDetail extends Component{
    constructor(props){
        super(props);
        this.state={
            isBegin: false,
            hasTimer: true
        }
    }
    componentDidMount(){

        getSkuInfo({
            "activityId": this.props.location.query.activityId,
            "sku": this.props.location.query.sku,
            "storeId": this.props.location.query.storeId
        }).then(data=>{
            console.log(data)
            this.setState({hasStock: !!data.data})
        })
    }
    getGoods = (item) => {
        // e.stopPropagation();
        // console.log(this.state.isBegin,this.state.hasStock);
        if (!this.state.isBegin){
            showModule(<FiveTwoZeroModule bodyText={"客官来早了哦~秒杀还未开始，<br>先去其他的活动转转吧~"} btnTitle="爆款专区" btnfun={this.toHot}/>);
            return false;
        }
        if(item.state===0){
            showModule(<FiveTwoZeroModule bodyText={"亲，手慢了哎，宝贝已抢光～<br>摸摸头明天再来呗～"} btnTitle="爆款专区" btnfun={this.toHot}/>);
            return false;
        }
        submitActivityOrder(Object.assign({},{...item},{num:1,mobile: this.state.mobile})).then(data=>{
            if (data.code==="10003"){
                showModule(<FiveTwoZeroModule bodyText={"亲，手慢了哎，宝贝已抢光～<br>摸摸头明天再来呗～"} btnTitle="爆款专区" btnfun={this.toHot}/>);
            } else if(data.code==="0000"){
                showModule(<FiveTwoZeroModule bodyText={`<div>亲，${item.goodsName}</div><div class='redModuleText'>已秒到名额</div>`} btnTitle="立即支付" btnfun={()=>weChatPay({
                    "orderId": data.orderId,
                    "payFee": data.payFee,
                    "body": data.body,
                    "payType": "MWEB",
                    "businessType": 'activity',
                    "attach": JSON.stringify({'attach':'activity'})
                },'http://yxcat.com.cn/ddp-activities/#management?activityId=20180520&mobile='+this.state.mobile)}/>);

            }else if(data.code==="1002"){
                defaultModule("提示",data.message, false,1)
            }else if(data.code==="1001"){
                showModule(<FiveTwoZeroModule bodyText={"客官来早了哦~秒杀还未开始，<br>先去其他的活动转转吧~"} btnTitle="爆款专区" btnfun={this.toHot}/>);
            }else{
                console.log(data);
                defaultModule("提示",data.message, false,1)
            }
        });

    }
    toPay = () =>{
        // WechatPay({
        //     "orderId": data.orderId,
        //     "payFee": data.payFee,
        //     "body": data.body,
        //     "payType": "MWEB",
        //     "businessType": 'activity',
        //     "attach": JSON.stringify({'attach':'activity'})
        // },'yxcat.com.cn/ddp-activities/#management?activityId=20180520&mobile='+this.state.mobile)
    }
    isBegin = (_second)=>{
        if(_second<0){
            this.setState({isBegin: true})
        }
        if(_second<0 && this.state.hasStock){
            this.setState({hasTimer: false});
        }
    }
    toOuter =()=>{
        console.log(JSON.parse(this.props.location.query.goodsNotice).url)
        if(this.props.location.query.goodsNotice && JSON.parse(this.props.location.query.goodsNotice).url){
            window.location.href = JSON.parse(this.props.location.query.goodsNotice).url
        }
    }
    render(){
        return(
            <div className="goodDetail">
                <img src={this.props.location.query.introduction}/>
                <div className="timerDiv">
                    <TimeOut hasTimer={this.state.hasTimer} datumTime={[10, 0, 0, 0]} getTimeValue={this.isBegin}/>
                </div>

                <button onClick={()=>this.getGoods({...this.props.location.query})}>立即秒杀</button>
                <div className="toOuter" onClick={this.toOuter}></div>
            </div>
        )
    }
}


